@import "../../styles/communs.scss";

.containerPrestation {
  position: relative;
  height: 400px;
  display: flex;
  width: 50%;
  align-self: center;
  //outline: 10px solid black;
 margin-left:20px;
}
.titlePrestation {
  font-size: 30px;
  font-weight: bold;
}

.txtPrestation {
  font-size: 20px;
}
.titlePageLeft{
  margin-top:-50px;
  font-size: 50px;
}

ul {
  list-style-type: none;
  perspective: 3900px;
}

ul li {
  display: grid;
  grid-template-columns: 20% auto;
  border-radius: 10px;
  padding: 14px;
  cursor: pointer;
  transform: rotateY(-50deg) rotateX(10deg);
  position: absolute;
  border-bottom: 4px solid rgba(0, 0, 0, 0.2);
  border-right: 4px solid rgba(0, 0, 0, 0.2);
  transition: 0.7s;
}
#right {
  margin-top: 50px;
}
ul li:nth-child(1) {
  color: $colorGrey;
  background-image: linear-gradient(135deg, #ffdcba, #7fc5df);
  top: -112px;
  z-index: 2;
  width: 700px;
  height: 120px;
}

ul li:nth-child(2) {
  background-image: linear-gradient(135deg, #7fc5df, #ffdcba);
  z-index: 1;
  color: $colorGrey;
  width: 700px;
  height: 120px;
}

ul li:nth-child(3) {
  color: $colorGrey;
  background-image: linear-gradient(135deg, #ffdcba, #7fc5df);
  top: 108px;
  z-index: 0;
  width: 700px;
  height: 130px;
}

ul li:hover {
  transform: translateY(30px) rotateY(-35deg) rotateX(15deg) scale(1.2);
  z-index: 3;
}
img {
  width: 70px;
}


#left > div {
  color: grey;
  line-height: 1.9em;
}

@media #{$medium-down} {
  .titlePageLeft{
    margin-top:-40px;
     font-size: 30px;
   }
   .titlePrestation {
     font-size: 20px;
     font-weight: bold;
   }
   
   .txtPrestation {
     font-size: 10px;
   }
   .containerTxtLeft
   {
    
    width:350px;
   }
   .containerPrestation{
    // flex-direction: column;
     height: 600px;
   }
      
   ul {
      perspective: 6000px;
   }
   
   ul li:nth-child(1) {
      width: 300px;
     height: 100px;
   }
   ul li:nth-child(2) {
       width: 300px;
     height: 100px;
     top: -25px;
   }
   
   ul li:nth-child(3) {
       width: 300px;
     height: 120px;
     top: 60px;
   }
   #right {
    margin-top:100px;
   }
   }

   @media #{$medium-down} {

    .containerPrestation{
      flex-direction: column;
    }
   }

