@import "../../styles/communs.scss";

.containerBanner {
 // outline:1px solid red;
  height: 400px;
  margin-bottom:200px;
  box-shadow: 15px 5px 5px 15px #ffdcba;

}
#backgroundBanner {
  width: 100%;
  margin: 0;
  padding: 400px 0 0 0;
  position: absolute;
  left: 0;
  overflow: hidden;
  background: url("http://res.cloudinary.com/duqzvuawc/image/upload/c_scale,h_424/v1508813537/clouds_tile_efhbuh.jpg");
  animation: clouds_animation 50s linear infinite;
}
@keyframes clouds_animation {
  0% {
    background-position: 0 0;
  }
  50% {
    background-position: -2122px 0;
  }

  100% {
    background-position: 0 0;
  }
}

.titleBanner {

  // padding-top: 35px;
  color: $colorGrey;
  position: relative;
  font-size: 50px;
  margin-top:50px;
  margin-left: 20px;
  margin-right: 20px;
}
.title1 {
  display: flex;
  justify-content: flex-start;
  padding-top:50px;
}
.title2 {
  display: flex;
  justify-content: center;
}
.title3 {
  display: flex;
  justify-content: flex-end;
}


@media #{$small-down} {
  .titleBanner{
    font-size:20px;
     }
     #backgroundBanner {
      padding: 300px 0 0 0;
     }
     .containerBanner {
      // outline:1px solid red;
       height: auto;
     }
  }